<template>
  <v-container fluid class="pa-0">
    <v-card height="200">
      <v-tabs color="danger" v-model="tab">
        <v-tab value="cmj">反向跳CMJ</v-tab>
        <v-tab value="sj">蹲踞跳SJ</v-tab>
        <v-tab value="rj">連續跳RJ</v-tab>
        <v-tab value="iso">等長測試ISO TEST</v-tab>
      </v-tabs>
      <v-divider></v-divider>
      <v-tabs-window v-model="tab">
        <v-tabs-window-item value="cmj">
          <div class="button-group pa-3">
            <v-btn v-for="item in items" :key="item.value" class="toggle-btn"
              :color="selected.includes(item.value) ? 'primary' : 'secondary'"
              :variant="selected.includes(item.value) ? 'flat' : 'outlined'" @click="toggleSelect(item.value)">
              {{ item.label }}
            </v-btn>
          </div>

          <div class="mt-4">
            已選擇：{{ selected }}
          </div>
        </v-tabs-window-item>
        <v-tabs-window-item value="sj">
          <v-sheet class="pa-5">敬請期待</v-sheet>
        </v-tabs-window-item>
        <v-tabs-window-item value="rj">
          <v-sheet class="pa-5">敬請期待</v-sheet>
        </v-tabs-window-item>
        <v-tabs-window-item value="iso">
          <v-sheet class="pa-5">敬請期待</v-sheet>
        </v-tabs-window-item>
      </v-tabs-window>
    </v-card>
    <v-card outlined class="pa-2 my-4">
      <v-btn @click="stackBarSwitch()" :color="(stackBar) ? 'primary' : ''">各向度趨勢分析</v-btn>
      <v-card-text>
        <div ref="chartRef" style="width: 100%; height: 400px;"></div>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script setup>
import { onMounted, ref, onBeforeUnmount } from "vue";
import { dashboardPhysicalFitness } from "@/requests";
import * as echarts from "echarts";

const stackBar = ref(false);
function stackBarSwitch() {
  stackBar.value = !stackBar.value;
  updateChart();
}

const tab = ref('cmj');

const items = [
  { label: '系統重量', value: '系統重量' },
  { label: '跳躍高度', value: '跳躍高度' },
  { label: '下蹲深度', value: '下蹲深度' },
  { label: '峰值推進力量', value: '峰值推進力量' },
  { label: '相對峰值推進力量', value: '相對峰值推進力量' },
  { label: '峰值制動力量', value: '峰值制動力量' },
  { label: '相對峰值制動力量', value: '相對峰值制動力量' },
  { label: '制動發力率', value: '制動發力率' },
  { label: '峰值速度', value: '峰值速度' },
  { label: '離地耗時', value: '離地耗時' },
  { label: '峰值功率', value: '峰值功率' },
  { label: '相對峰值功率', value: '相對峰值功率' },
  { label: '修正式', value: '修正式' },
  { label: '反應肌力指數', value: '反應肌力指數' },
  { label: '相對推進淨值衝量', value: '相對推進淨值衝量' },
  { label: '相對制動淨值衝量', value: '相對制動淨值衝量' },
]
const selected = ref([]);

function toggleSelect(value) {
  const index = selected.value.indexOf(value)
  if (index === -1) {
    selected.value.push(value)
  } else {
    selected.value.splice(index, 1)
  }
}

const props = defineProps({
  user: {
    type: Object,
    default: () => ({})
  }
});

const chartRef = ref(null);
let chart = null;

// --- 範例資料（你可以替換成真實資料） ---
const categories = ref(['2025-01', '2025-02', '2025-03', '2025-04', '2025-05']);

// 柱狀資料
const barSeriesNames = ['A專項體能', 'B一般體能', 'C重量訓練', 'D自助練習'];
const barSeriesData = ref([
  [120, 200, 150, 80, 70],
  [60, 80, 70, 50, 40],
  [30, 40, 20, 10, 5],
  [30, 40, 20, 10, 5]
]);

// 折線資料
const lineName = ref(['轉換率']);
const lineData = [3.2, 4.1, 3.7, 2.9, 3.4];
// -------------------------------------------
const lineSeries = ref([{
    name: lineName,
    type: 'line',
    data: lineData,
    yAxisIndex: 1,
    // smooth: false,
    // symbol: 'circle',
    // symbolSize: 8,
    // lineStyle: { width: 2 }
  }]);
function getOption() {
  const barSeries = (stackBar.value) ? barSeriesNames.map((name, idx) => ({
    name,
    type: 'bar',
    stack: 'bars_stack',
    data: barSeriesData.value[idx],
    emphasis: { focus: 'series' }
  })) : [];

  return {
    tooltip: {
      trigger: 'axis',
      axisPointer: { type: 'shadow' }
    },
    legend: {
      data: [...(stackBar.value) ? barSeriesNames : [], ...lineName.value],
      top: 0
    },
    grid: {
      left: '6%',
      right: '6%',
      bottom: '8%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: categories.value
    },
    yAxis: [
      {
        type: 'value',
        name: '數量（柱狀）',
        position: 'left'
      },
      {
        type: 'value',
        name: '比率（折線）',
        position: 'right',
        axisLabel: {
          formatter: '{value}'
        }
      }
    ],
    series: [
      ...barSeries,
      ...lineSeries.value
    ]
  }
}

onMounted(() => {
  // updateChart()
});

function updateChart() {
  chart?.dispose()
  chart = null
  chart = echarts.init(chartRef.value)
  chart.setOption(getOption())

  // 若視窗改變尺寸，chart 也跟著 resize
  window.addEventListener('resize', () => chart && chart.resize())
};

onBeforeUnmount(() => {
  if (chart) {
    window.removeEventListener('resize', () => chart && chart.resize())
    chart.dispose()
    chart = null
  }
})

function handleSearch(data) {
  dashboardPhysicalFitness.getForcePlate({ userId: props.user.id, sDate: data.sDate, eDate: data.eDate, pickedDataList: selected.value })
    .then(response => {
      // 假設 response.data 包含了更新圖表所需的資料
      // 你可以根據實際資料結構來更新 categories、barSeriesData 和 lineData
      categories.value = response.data.categories;
      barSeriesData.value = response.data.barSeriesData;
      lineSeries.value = response.data.lineSeries;

      updateChart();
    })
    .catch(error => {
      console.error("Error fetching Force Plate data:", error);
    });
  // const userNameList = props.userList.map(user => user.name);
  // const userIdList = props.userList.map(user => user.id);
  // dashboardPhysicalFitness.getAcwrCompare({ filters: filters, userNameList: userNameList, userIdList: userIdList, sDate: data.sDate, eDate: data.eDate })
  //   .then(response => {
  //     datasetsource.value = response.data.sourceData;
  //     userNameList.value = response.data.memberList;
  //     xAxisData.value = response.data.xAxisData;
  //     acwrCompareSeries.value = response.data.acwrCompareSeries;
  //     updateChart();
  //   })
}

defineExpose({
  handleSearch
});
</script>

<style scoped>
.button-group {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  /* 控制按鈕間距 */
}

.toggle-btn {
  min-width: 120px;
  /* 每個按鈕寬度一致 */
  justify-content: center;
  border-radius: 6px;
  font-weight: 500;
}
</style>