<template>
  <v-container fluid class="pa-0">
    <!-- 上半圖：短期/長期負荷 -->
    <v-card outlined class="pa-2 mb-4">
      <v-col cols="6">
        <v-row>
          <v-select class="pa-2" v-model="selectAction" :items="actionOptions" item-title="title" item-value="value"
            label="測驗動作" variant="outlined"></v-select>
          <v-select class="pa-2" v-model="selectParam" :items="paramOptions" item-title="title" item-value="value"
            label="參數" variant="outlined"></v-select>
        </v-row>
      </v-col>
      <v-spacer></v-spacer>
      <v-col cols="12">
        <v-row>
          <div ref="cmjRadarRef" style="width: 100%; height: 600px;"></div>
        </v-row>
      </v-col>
    </v-card>
  </v-container>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { dashboardPhysicalFitness } from "@/requests";
import * as echarts from "echarts";

const props = defineProps({
  userList: {
    type: Array,
    required: true
  }
})

const actionOptions = ref([
  { title: 'CMJ', value: 'cmj' },
  { title: 'SJ', value: 'sj' },
  { title: 'SP', value: 'sp' }
])

const paramOptions = ref([
  { title: '跳躍高度', value: 'jumpHeight' },
  { title: '離地耗時', value: 'timeOffGround' },
  { title: '系統重量', value: 'systemWeight' },
  { title: '峰值推進力量', value: 'peakPropulsiveForce' },
  { title: '推進發力率', value: 'propulsivePowerRate' }
])

const cmjRaderSeries = ref([]);

const cmjRadarRef = ref(null);
let cmjRadarInstance = null;

const optionCmjChart = () => {
  if (!cmjRadarInstance) return;
  const optionCmj = {
    tooltip: {
      trigger: "axis",
    },
    legend: {
      data: props.userList.map(user => user.name),
      top: 0
    },
    radar: {
      // shape: 'circle',
      indicator: [
        { name: '推進發力率', max: 100 },
        { name: 'SJ跳躍高度', max: 100 },
        { name: 'CMJ跳躍高度', max: 100 },
        { name: '系統重量', max: 1000 },
        { name: '離地耗時', max: 1000 },
        { name: '峰值推進力量', max: 5000 }
      ]
    },
    series: cmjRaderSeries.value
  };
  cmjRadarInstance.setOption(optionCmj);
};

function updateChart() {
  cmjRadarInstance?.dispose();
  cmjRadarInstance = echarts.init(cmjRadarRef.value);
  optionCmjChart();
}
onMounted(() => {
  updateChart();
})

function handleSearch(data) {
  const userNameList = data.selectedMembers.map(user => user.name);
  const userIdList = data.selectedMembers.map(user => user.id);
  dashboardPhysicalFitness.getCmjAnalysis({ userNameList: userNameList, userIdList: userIdList, mDate: data.mDate })
    .then(response => {
      cmjRaderSeries.value = response.data.series;
      updateChart();
    })
}

defineExpose({
  handleSearch
});
</script>

<style scoped></style>