<template>
  <v-card class="id-card" outlined>
    <v-card-text class="pa-0">
      <v-container fluid class="pa-0">
        <v-row dense no-gutters>
          <v-col cols="3" class="text-center pa-1">
            <v-avatar size="100">
              <v-img :src="user.avatar" alt="Avatar"></v-img>
            </v-avatar>
          </v-col>
          <v-col cols="2" class="pa-1">
            <v-list dense class="pa-0">
              <v-list-item class="px-1 min-height-auto">
                <v-list-item-title class="text-body-2" style="white-space: nowrap; overflow: visible;">姓名: {{ user.name }}</v-list-item-title>
              </v-list-item>
              <v-list-item class="px-1 min-height-auto">
                <v-list-item-title class="text-body-2" style="white-space: nowrap; overflow: visible;">性別: {{ user.gender }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-col>
          <v-col cols="4" class="pa-1">
            <v-list dense class="pa-0">
              <v-list-item class="px-1 min-height-auto">
                <v-list-item-title class="text-body-2" style="white-space: nowrap; overflow: visible;">隊伍: {{ user.team }}</v-list-item-title>
              </v-list-item>
              <v-list-item class="px-1 min-height-auto">
                <v-list-item-title class="text-body-2" style="white-space: nowrap; overflow: visible;">生日: {{ user.birthday }} ({{ age }}歲)</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-col>
          <v-col cols="3" class="pa-1">
            <v-list dense class="pa-0">
              <v-list-item class="px-1 min-height-auto">
                <v-list-item-title class="text-body-2" style="white-space: nowrap; overflow: visible;">身高: {{ user.height }} cm</v-list-item-title>
              </v-list-item>
              <v-list-item class="px-1 min-height-auto">
                <v-list-item-title class="text-body-2" style="white-space: nowrap; overflow: visible;">體重: {{ user.weight }} kg</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-col>
        </v-row>
      </v-container>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { computed } from 'vue';

const props = defineProps({
  user: {
    type: Object,
    required: true,
    default: () => ({
      avatar: "https://via.placeholder.com/10",
      name: "王小明",
      gender: "男",
      team: "紅隊",
      birthday: "1990-01-01",
      height: 175,
      weight: 70,
    }),
  },
});

const age = computed(() => {
  const birthDate = new Date(props.user.birthday);
  const today = new Date();
  let age = today.getFullYear() - birthDate.getFullYear();
  const monthDiff = today.getMonth() - birthDate.getMonth();
  if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthDate.getDate())) {
    age--;
  }
  return age;
});
</script>

<style scoped>
.id-card {
  /* max-width: 35vw; */
  /* min-width: 300px; */
  /* margin: auto; */
}
</style>
