<template>
  <v-container fluid class="pa-0">
    <!-- 上半圖：短期/長期負荷 -->
    <v-card outlined class="pa-2 mb-4">
      <v-card-title class="text-h6">ACWR趨勢</v-card-title>
      <v-card-text>
        <div ref="loadChart" style="width: 100%; height: 400px;"></div>
        <div ref="acwrChart" style="width: 100%; height: 400px;"></div>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script setup>
import { onMounted, ref, onBeforeUnmount } from "vue";
import * as echarts from "echarts";

const loadChart = ref(null);
const acwrChart = ref(null);

let loadChartInstance = null;
let acwrChartInstance = null;

const props = defineProps({
  categories: {
    type: Array,
    default: () => []
  },
  shortTermData: {
    type: Array,
    default: () => [],
  },
  longTermData: {
    type: Array,
    default: () => [],
  },
  acwrData: {
    type: Array,
    default: () => [],
  },
  xAxisData: {
    type: Array,
    default: () => []
  }
});

const optionLoadChartconst = () => {
  if (!loadChartInstance) return;
  const optionLoadChart = {
    title: { text: "" },
    tooltip: {
      trigger: "axis",
      axisPointer: { type: "cross", label: { backgroundColor: "#6a7985" } },
    },
    graphic: [
      {
        type: "group",
        left: "2%",
        top: "middle",
        rotation: Math.PI / 2, // 🔄 旋轉 90 度
        children: [
          {
            type: "rect",
            shape: { width: 20, height: 10 },
            style: { fill: "#5470c6" },
          },
          {
            type: "text",
            left: 30,
            top: 0,
            style: { text: "短期負荷(日)", fill: "#000", font: "14px sans-serif" },
          },
        ],
      },
      {
        type: "group",
        right: "2%",
        top: "middle",
        rotation: Math.PI / 2,
        children: [
          {
            type: "rect",
            shape: { width: 20, height: 10 },
            style: { fill: "#91cc75" },
          },
          {
            type: "text",
            left: 30,
            top: 0,
            style: { text: "長期負荷(日)", fill: "#000", font: "14px sans-serif" },
          },
        ],
      },
      {
        type: "line",
        left: "14.5%",
        top: "-100%",
        shape: { x1: 0, y1: 0, x2: 0, y2: 1000 },
        style: { stroke: "#000", lineWidth: 1 },
      },
    ],
    toolbox: { feature: { saveAsImage: {} } },
    grid: { left: "15%", right: "10%", bottom: "10%", containLabel: false },
    xAxis: [
      {
        type: "category",
        boundaryGap: false,
        data: props.xAxisData,
        axisLabel: {
          interval: 'auto',
          // rotate: 45
          align: 'end',
        },
      },
    ],
    yAxis: [{ type: "value" }],
    series: [
      {
        name: "短期負荷(日)",
        type: "line",
        stack: "Total",
        areaStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: "#60B7C7" },
            { offset: 0.1, color: "#EEFCFF" },
          ]),
        },
        emphasis: { focus: "series" },
        data: props.shortTermData,
      },
      {
        name: "長期負荷(日)",
        type: "line",
        stack: "Total",
        areaStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: "#8BF213" },
            { offset: 0.1, color: "#E9FFCF" },
          ]),
        },
        emphasis: { focus: "series" },
        data: props.longTermData,
      },
    ],
  };
  loadChartInstance.setOption(optionLoadChart);
};

const optionAcwrChat = () => {
  if (!acwrChartInstance) return;

  const optionAcwrChart = {
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      data: ['ACWR(日)'],
    },
    grid: { left: "15%", right: "10%", bottom: "10%", top: "0%", containLabel: false },
    xAxis: [
      {
        type: "category",
        boundaryGap: false,
        data: props.xAxisData,
        show: false,
      },
    ],
    yAxis: [{ type: "value" }],
    series: [
      {
        type: "line",
        data: props.acwrData,
        lineStyle: { color: "#5470c6", width: 2 },
      },
    ],
    graphic: [
      {
        type: "group",
        left: "2%",
        top: "middle",
        rotation: Math.PI / 2, // 🔄 旋轉 90 度
        children: [
          {
            type: "rect",
            shape: { width: 20, height: 10 },
            style: { fill: "#5470c6" },
          },
          {
            type: "text",
            left: 30,
            top: 0,
            style: { text: "ACWR(日)", fill: "#000", font: "14px sans-serif" },
          },
        ],
      },
      {
        type: "line",
        left: "14.5%",
        top: "-100%",
        shape: { x1: 0, y1: 0, x2: 0, y2: 1000 },
        style: { stroke: "#000", lineWidth: 1 },
      },
    ],
  };

  acwrChartInstance.setOption(optionAcwrChart);
};
onMounted(() => {
  updateChart()
});

function updateChart() {
  loadChartInstance = echarts.init(loadChart.value);
  optionLoadChartconst();

  acwrChartInstance = echarts.init(acwrChart.value);
  optionAcwrChat();

  // 自動調整大小
  window.addEventListener("resize", resizeChart);
};

watch(
  () => [props.categories, props.xAxisData, props.shortTermData, props.longTermData, props.acwrData],
  () => {
    // console.log("category:", props.categories);
    // console.log("xAxisData:", props.xAxisData);
    // console.log("shortTermData:", props.shortTermData);
    // console.log("longTermData:", props.longTermData);
    // console.log("acwrData:", props.acwrData);
    updateChart();
  },
  { deep: true }
)

onBeforeUnmount(() => {
  window.removeEventListener("resize", resizeChart);
  loadChartInstance?.dispose();
  acwrChartInstance?.dispose();
});

function resizeChart() {
  loadChartInstance?.resize();
  acwrChartInstance?.resize();
}
</script>

<style scoped></style>