<template>
  <v-card class="board-card" outlined>
    <v-card-text>
      <SearchBar @search="handleSearch" :show-date-picker="selected != 'cmjAnalysis'" :sDate="defaultStartDate"
        :date-picker-type="datePickerType" :member-picker-type="memberPickerType" :member-options="memberOptions" :eDate="defaultEndDate">
        <template #icon>
          <v-icon>mdi-arm-flex</v-icon>
        </template>
        <template #title>
          體能監控
        </template>

        <v-select hide-details="auto" variant="outlined" v-model="selected" :items="options" label="選擇分析項目"
          @update:model-value="onSelectChange" density="compact" style="min-width: 200px;" />

        <select>
          <option>網球隊</option>
        </select>


      </SearchBar>
      <AcwrPersonal ref="acwrPersonalRef" v-if="selected === 'acwrPersonal'" :user="user" />
      <AcwrCompare ref="acwrCompareRef" v-if="selected === 'acwrCompare'" :userList="users" />
      <CmjAnalysis ref="cmjAnalysisRef" v-if="selected === 'cmjAnalysis'" :userList="users" />
      <ForcePlate ref="forcePlateRef" v-if="selected === 'forcePlate'" :user="user" />
    </v-card-text>
  </v-card>
</template>

<script setup>
import SearchBar from "../components/searchBar.vue";
import AcwrPersonal from "./acwrPersonal.vue";
import AcwrCompare from "./acwrCompare.vue";
import CmjAnalysis from "./cmjAnalysis.vue";
import ForcePlate from "./forcePlate.vue";
import { dashboardPhysicalFitness } from "@/requests";
import { onMounted } from "vue";

onMounted(() => {
  dashboardPhysicalFitness.getMemberList().then(response => {
    memberOptions.value = response.data;
  });
});
const user = ref();
const users = ref([]);
const defaultStartDate = ref(new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString().slice(0, 10));
const defaultEndDate = ref(new Date().toISOString().slice(0, 10));
const datePickerType = computed(() => {
  const option = options.find(opt => opt.value === selected.value);
  return option ? option.datePickerType : 'date';
});
const memberPickerType = computed(() => {
  const option = options.find(opt => opt.value === selected.value);
  return option ? option.memberPickerType : 'single';
});
const options = [
  { title: 'ACWR個人分析', value: 'acwrPersonal', datePickerType: 'date', memberPickerType: 'single' },
  { title: 'ACWR比較分析', value: 'acwrCompare', datePickerType: 'date', memberPickerType: 'multiple' },
  { title: 'CMJ比較分析', value: 'cmjAnalysis', datePickerType: 'month', memberPickerType: 'multiple' },
  { title: '測力板', value: 'forcePlate', datePickerType: 'date', memberPickerType: 'single' },
]
const selected = ref('acwrPersonal');

const memberOptions = ref([]);

const acwrPersonalRef = ref(null);
const acwrCompareRef = ref(null);
const cmjAnalysisRef = ref(null);
const forcePlateRef = ref(null);
function handleSearch(data) {
  switch (selected.value) {
    case 'acwrPersonal':
      user.value = data.selectedMember;
      acwrPersonalRef.value.handleSearch(data);
      break;
    case 'acwrCompare':
      users.value = data.selectedMembers;
      acwrCompareRef.value.handleSearch(data);
      break;
    case 'cmjAnalysis':
      cmjAnalysisRef.value.handleSearch(data);
      break;
    case 'forcePlate':
      forcePlateRef.value.handleSearch(data);
      break;
  }
}

</script>

<style scoped>
.board-card {
  margin: 8px;
  width: 100%;
  /* min-width: 600px; */
}
</style>
