<template>
  <v-card class="acwr-card" outlined>
    <v-card-text class="pa-0">
      <v-container class="pa-0">
        <!-- 上方區塊 -->
        <v-row class="align-center justify-center position-relative ma-0">
          <!-- 置中：標題 + 提示圖示 -->
          <v-col cols="auto" class="d-flex align-center justify-center">
            <span style="font-weight: bold; color: #1F6C84; font-size: 20px;">ACWR值</span>
            <v-tooltip location="top" open-delay="300">
              <template #activator="{ props }">
                <v-icon v-bind="props" icon="mdi-alert-circle" color="#1F6C84" class="ml-2" />
              </template>
              <div>
                <div>● 「短期的訓練量」/「長期平均訓練量」</div>
                <div>● 預設計算當日ACWR值 (「近一週總訓練量」/「近四週平均訓練量」)</div>
              </div>
            </v-tooltip>
          </v-col>

          <!-- 右上角 menu -->
          <v-col cols="auto" class="position-absolute" style="right: 0;">
            <v-menu v-model="menuVM" :close-on-content-click="false" location="bottom end" transition="scale-transition">
              <template #activator="{ props }">
                <v-icon v-bind="props" color="primary" icon="mdi-menu" class="cursor-pointer" />
              </template>

              <v-card min-width="300px">
                <v-card-text>
                  <v-row>
                    <v-col cols="12">
                      <v-menu v-model="shortTermMenu" :close-on-content-click="false" transition="scale-transition">
                        <template #activator="{ props }">
                          <v-text-field hide-details="auto" v-bind="props" v-model="shortTermRangeText" readonly
                            density="compact" append-inner-icon="mdi-calendar" label="短期" variant="outlined"
                            placeholder="選擇日期範圍" />
                        </template>

                        <v-card>
                          <v-date-picker v-model="shortTermRange" multiple="range" scrollable>
                            <template #actions>
                              <v-btn text color="primary" @click="shortTermMenu = false">確定</v-btn>
                            </template>
                          </v-date-picker>
                        </v-card>
                      </v-menu>
                    </v-col>

                    <v-col cols="12">
                      <v-menu v-model="longTermMenu" :close-on-content-click="false" transition="scale-transition">
                        <template #activator="{ props }">
                          <v-text-field hide-details="auto" v-bind="props" v-model="longTermRangeText" readonly
                            density="compact" append-inner-icon="mdi-calendar" label="長期" variant="outlined"
                            placeholder="選擇日期範圍" />
                        </template>

                        <v-card>
                          <v-date-picker v-model="longTermRange" multiple="range" scrollable>
                            <template #actions>
                              <v-btn text color="primary" @click="longTermMenu = false">確定</v-btn>
                            </template>
                          </v-date-picker>
                        </v-card>
                      </v-menu>
                    </v-col>
                  </v-row>
                  <v-row justify="center" class="mt-4">
                    <v-btn variant="flat" color="primary" @click="dateConfirm" block >
                      確認
                    </v-btn>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-menu>
          </v-col>
        </v-row>

        <v-row justify="center" class="my-4">
          <span style="font-weight: bold; color: #1F6C84; font-size: 48px;">{{ acwrValue }}</span>
        </v-row>

        <v-row justify="center" class="my-4">
          <span style="font-weight: bold; color: #1F6C84; font-size: 20px;">{{ yesterday }}</span>
        </v-row>

      </v-container>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
const props = defineProps({
  acwrValue: {
    type: Number,
    default: 0
  }
})

const yesterday = computed(() => {
  const date = new Date()
  date.setDate(date.getDate() - 1)
  return date.toISOString().split('T')[0]
})
const shortTermMenu = ref(false)
const shortTermRange = ref([])
const shortTermRangeText = computed(() => {
  if (!shortTermRange.value) return ''
  if (Array.isArray(shortTermRange.value) && shortTermRange.value.length > 0) {
    const dates = shortTermRange.value.map(date => new Date(date).toISOString().split('T')[0])
    return dates.length > 1 ? `${dates[0]} ~ ${dates[dates.length - 1]}` : dates[0]
  }
  return new Date(shortTermRange.value).toISOString().split('T')[0]
})
const longTermMenu = ref(false)
const longTermRange = ref([])
const longTermRangeText = computed(() => {
  if (!longTermRange.value) return ''
  if (Array.isArray(longTermRange.value) && longTermRange.value.length > 0) {
    const dates = longTermRange.value.map(date => new Date(date).toISOString().split('T')[0])
    return dates.length > 1 ? `${dates[0]} ~ ${dates[dates.length - 1]}` : dates[0]
  }
  return new Date(longTermRange.value).toISOString().split('T')[0]
})

function pickDates(number) {
  const today = new Date()
  today.setDate(today.getDate() - 1)
  const dates = []
  for (let i = (number - 1); i >= 0; i--) {
    const date = new Date()
    date.setDate(today.getDate() - i)
    dates.push(date)
  }
  return dates;
}
const emit = defineEmits(['dateConfirm'])
const menuVM = ref(false);
function dateConfirm() {
  emit('dateConfirm', {
    shortTermRange: shortTermRange.value,
    longTermRange: longTermRange.value
  });
  menuVM.value = false;
}

onMounted(() => {
  shortTermRange.value = pickDates(7);
  longTermRange.value = pickDates(28);
})
</script>

<style scoped>
.acwr-card {
  /* min-width: 360px; */
}
</style>
