<template>
  <v-container fluid class="pa-0">
    <!-- 上半圖：短期/長期負荷 -->
    <v-card outlined class="pa-2 mb-4">
      <div class="my-4 text-center">
        <v-btn class="pa-1 mx-1" :color="filters.total ? 'primary' : 'grey'" @click="typeChange('total')">
          總量
        </v-btn>
        <v-btn class="pa-1 mx-1" :color="filters.typeA ? 'primary' : 'grey'" @click="typeChange('typeA')">
          A專項體能
        </v-btn>
        <v-btn class="pa-1 mx-1" :color="filters.typeB ? 'primary' : 'grey'" @click="typeChange('typeB')">
          B一般體能
        </v-btn>
        <v-btn class="pa-1 mx-1" :color="filters.typeC ? 'primary' : 'grey'" @click="typeChange('typeC')">
          C重量訓練
        </v-btn>
        <v-btn class="pa-1 mx-1" :color="filters.typeD ? 'primary' : 'grey'" @click="typeChange('typeD')">
          D自助練習
        </v-btn>
      </div>
      <v-card-text>
        <div ref="loadChartCompare" style="width: 100%; height: 400px;"></div>
        <div ref="acwrChartCompare" style="width: 100%; height: 400px;"></div>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script setup>
import { onMounted, ref, onBeforeUnmount } from "vue";
import { dashboardPhysicalFitness } from "@/requests";
import * as echarts from "echarts";

const loadChartCompare = ref(null);
const acwrChartCompare = ref(null);

let loadChartCompareInstance = null;
let acwrChartCompareInstance = null;

const filters = reactive({
  total: true,
  typeA: true,
  typeB: true,
  typeC: true,
  typeD: true,
});

function typeChange(type) {
  switch (type) {
    case "total":
      filters.total = !filters.total;
      filters.typeA = filters.total;
      filters.typeB = filters.total;
      filters.typeC = filters.total;
      filters.typeD = filters.total;
      break;
    case "typeA":
      filters.typeA = !filters.typeA;
      break;
    case "typeB":
      filters.typeB = !filters.typeB;
      break;
    case "typeC":
      filters.typeC = !filters.typeC;
      break;
    case "typeD":
      filters.typeD = !filters.typeD;
      break;
    default:
      break;
  }
}

const props = defineProps({
  userList: {
    type: Array,
    default: () => []
  },
});
const xAxisData = ref([]);
const acwrCompareSeries = ref([]);
const datasetsource = ref([]);
const optionLoadChartconst = () => {
  if (!loadChartCompareInstance) return;
  const optionLoadChart = {
    tooltip: {
      trigger: "axis",
    },
    legend: {
      top: 0
    },
    dataset: {
      source: datasetsource.value
    },
    xAxis: { type: "category" },
    yAxis: {},
    series: datasetsource.value.length > 0
      ? datasetsource.value[0].slice(1).map(() => ({ type: 'bar' }))
      : [] // 如果還沒資料，就給空陣列
  };
  loadChartCompareInstance.setOption(optionLoadChart);
};

const optionAcwrChat = () => {
  if (!acwrChartCompareInstance) return;

  const optionAcwrChart = {
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      data: props.userList,
    },
    // grid: { left: "15%", right: "10%", bottom: "10%", top: "0%", containLabel: false },
    xAxis: [
      {
        type: "category",
        boundaryGap: false,
        data: xAxisData.value,
        show: true,
      },
    ],
    yAxis: [{ type: "value" }],
    series: acwrCompareSeries.value,
  };

  acwrChartCompareInstance.setOption(optionAcwrChart);
};
onMounted(() => {
  // updateChart()
});

function updateChart() {
  loadChartCompareInstance?.dispose();
  acwrChartCompareInstance?.dispose();
  loadChartCompareInstance = echarts.init(loadChartCompare.value);
  optionLoadChartconst();
  acwrChartCompareInstance = echarts.init(acwrChartCompare.value);
  optionAcwrChat();

  // 自動調整大小
  window.addEventListener("resize", resizeChart);
};


onBeforeUnmount(() => {
  window.removeEventListener("resize", resizeChart);
  loadChartCompareInstance?.dispose();
  acwrChartCompareInstance?.dispose();
});

function resizeChart() {
  loadChartCompareInstance?.resize();
  acwrChartCompaerInstance?.resize();
}

function handleSearch(data) {
  const userNameList = props.userList.map(user => user.name);
  const userIdList = props.userList.map(user => user.id);
  dashboardPhysicalFitness.getAcwrCompare({ filters: filters, userNameList: userNameList, userIdList: userIdList, sDate: data.sDate, eDate: data.eDate })
    .then(response => {
      datasetsource.value = response.data.sourceData;
      userNameList.value = response.data.memberList;
      xAxisData.value = response.data.xAxisData;
      acwrCompareSeries.value = response.data.acwrCompareSeries;
      updateChart();
    })
}

defineExpose({
  handleSearch
});
</script>

<style scoped></style>