<template>
  <div ref="chartRef" style="width: 100%; height: 400px;"></div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue'
import * as echarts from 'echarts'
import { watch } from 'vue'

const chartRef = ref(null)
let chartInstance = null

const props = defineProps({
  categories: {
    type: Array,
    default: () => []
  },
  rawData: {
    type: Array,
    default: () => []
  },
  xAxisData: {
    type: Array,
    default: () => []
  }
})

// 初始化圖表
const initChart = () => {
  if (!chartRef.value) return
  chartInstance = echarts.init(chartRef.value)
  setChartOption()
}

// 設定圖表 option
const setChartOption = () => {
  if (!props.rawData.length) return

  const totalData = []
  for (let i = 0; i < props.rawData[0].length; ++i) {
    let sum = 0
    for (let j = 0; j < props.rawData.length; ++j) {
      sum += props.rawData[j][i]
    }
    totalData.push(sum)
  }

  const series = props.categories.map((name, sid) => ({
    name,
    type: 'bar',
    stack: 'total',
    barWidth: '60%',
    label: {
      show: true,
      formatter: (params) => Math.round(params.value * 1000) / 10 + '%'
    },
    data: props.rawData[sid].map((d, did) =>
      totalData[did] <= 0 ? 0 : d / totalData[did]
    )
  }))

  const option = {
    title: {
      text: '各項度趨勢分析',
      top: 0
    },
    tooltip: {
      trigger: 'axis',
      axisPointer: { type: 'shadow' }
    },
    legend: {
      data: props.categories,
      top: 30
    },
    grid: {
      left: '10%',
      right: '10%',
      bottom: '3%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: props.xAxisData,
      axisLabel: {
        rotate: 45
      }
    },
    yAxis: {
      type: 'value'
    },
    series
  }

  chartInstance.setOption(option)
}

// 監聽 props 改變時更新圖表
watch(
  () => [props.rawData, props.categories, props.xAxisData],
  () => {
    if (chartInstance) {
      chartInstance.clear();
      setChartOption();
    }
  },
  { deep: true }
)

// 初始化 & 銷毀
onMounted(() => {
  initChart()
  window.addEventListener('resize', () => chartInstance?.resize())
})

onBeforeUnmount(() => {
  chartInstance?.dispose()
})
</script>