<template>
  <v-card class="board-card" outlined>
    <v-card-text>
      <v-row>
        <v-col cols="6">
          <Personal :user="props.user" />
          <div class="my-4 text-center">
            <v-btn class="pa-1 mx-1" :color="filters.typeA ? 'primary' : 'grey'" @click="typeChange('typeA')">
              A專項體能
            </v-btn>
            <v-btn class="pa-1 mx-1" :color="filters.typeB ? 'primary' : 'grey'" @click="typeChange('typeB')">
              B一般體能
            </v-btn>
            <v-btn class="pa-1 mx-1" :color="filters.typeC ? 'primary' : 'grey'" @click="typeChange('typeC')">
              C重量訓練
            </v-btn>
            <v-btn class="pa-1 mx-1" :color="filters.typeD ? 'primary' : 'grey'" @click="typeChange('typeD')">
              D自助練習
            </v-btn>
          </div>
          <Acwr class="mt-4" :acwr-value="acwrValue" @dateConfirm="onDateConfirm"></Acwr>
          <Stackbar class="mt-4" :categories="categories" :x-axis-data="xAxisDataStackbar"
            :raw-data="rawDataStackbar" />
        </v-col>
        <v-col cols="6">
          <AcwrTrend class="mb-4" :categories="categories" :x-axis-data="xAxisDataAcwrTrend"
            :short-term-data="shortTermTrend" :long-term-data="longTermTrend" :acwr-data="acwrData" />
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { reactive, ref, watch } from "vue";
import Personal from "./components/personal.vue";
import Acwr from "./components/acwr.vue";
import Stackbar from "./components/stackbar.vue";
import AcwrTrend from "./components/acwrTrend.vue";
import { dashboardPhysicalFitness } from "@/requests";

const props = defineProps({
  user: {
    type: Object,
    default: () => ({})
  }
});

const filters = reactive({
  typeA: true,
  typeB: true,
  typeC: true,
  typeD: true,
});

const categories = ref([]);
const rawDataStackbar = ref([]);
const xAxisDataStackbar = ref([]);
const xAxisDataAcwrTrend = ref(['2025/01/01', '2025/01/02', '2025/01/03', '2025/01/04', '2025/01/05', '2025/01/06', '2025/01/07']);

const shortTermTrend = ref([]);
const longTermTrend = ref([]);
const acwrData = ref([]);

const acwrValue = ref(18);

function onDateConfirm(data) {
  if (data.shortTermRange.length > 0 && data.longTermRange.length > 0) {
    dashboardPhysicalFitness.getACWR({ filters: filters, userId: props.user.id, shortTerm: data.shortTermRange, longTerm: data.longTermRange })
      .then(response => {
        acwrValue.value = response.data;
      })
      .catch(error => {
        console.error("Error fetching ACWR data:", error);
      });
  }
};

function handleSearch(data) {

  dashboardPhysicalFitness.getStackbar({ filters: filters, userId: props.user.id, sDate: data.sDate, eDate: data.eDate })
    .then(response => {
      rawDataStackbar.value = response.data.data;
      xAxisDataStackbar.value = response.data.xAxisData;
      categories.value = response.data.types;
      xAxisDataStackbar.value = response.data.xAxisData;
    })

  dashboardPhysicalFitness.getAcwrTrend({ filters: filters, userId: props.user.id, sDate: data.sDate, eDate: data.eDate })
    .then(response => {
      console.log("AcwrTrend data fetched successfully:", response);
      shortTermTrend.value = response.data.shortTerm;
      longTermTrend.value = response.data.longTerm;
      acwrData.value = response.data.acwr;
      xAxisDataAcwrTrend.value = response.data.xAxisData;
    })
}
function typeChange(type) {
  switch (type) {
    case "typeA":
      filters.typeA = !filters.typeA;
      break;
    case "typeB":
      filters.typeB = !filters.typeB;
      break;
    case "typeC":
      filters.typeC = !filters.typeC;
      break;
    case "typeD":
      filters.typeD = !filters.typeD;
      break;
    default:
      break;
  }
}
defineExpose({
  handleSearch
});
</script>

<style scoped>
.board-card {
  margin: 8px;
  width: 100%;
}
</style>
